.header {
    display: flex;
    align-items: center;
    background-color: var(--header-bg);
    padding: 5px 20px;
    position: sticky;
    top: 0;
    padding-bottom: 25px;
    z-index: 99;
}

.header__logo {
    height: 50px;
    border: 1px solid transparent;
    padding: 5px;
    cursor: pointer;
}

.header__logo:hover {
    border: 1px solid white;
}

.header__deliveryaddress {
    display: flex;
    margin: 0 20px;
    cursor: pointer;
    border: 1px solid transparent;
    padding: 10px;
}

.header__deliveryaddress:hover {
    border: 1px solid white;
}

.header__deliveryaddress--location {
    color: white;
    display: flex;
    align-items: flex-end;
    font-size: 1.25rem;
}

.header__deliveryaddress--text {
    color: white;
}

.header--text {
    font-size: 12px;
    line-height: 14px;
}

.header--textbig {
    font-size: 14px;
    font-weight: bold;
    line-height: 15px;
}

.header__search {
    display: flex;
    align-items: center;
    background-color: white;
    flex: 1;
    border-radius: 10px;
    border: 3px solid var(--header-bg);
}

.header__search--input {
    line-height: 15px;
    padding: 7px 10px 10px 0;
    border: none;
    outline: none;
    margin-left: 10px;
    flex: 1;
}

.header__search--button {
    background-color: var(--orange);
    padding: 6px 10px;
    font-size: 1.25rem;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
}

.header__nav {
    display: flex;
    align-items: center;
}

.header__option {
    margin: 0 20px;
    color: white;
    cursor: pointer;
    border: 1px solid transparent;
    padding: 5px;
    position: relative;
}

.header__option:hover {
    border: 1px solid white;
}

.youraccount {
    position: absolute;
    top: 110%;
    width: 100%;
    transition: .4s ease-in-out;
    background: white;
    color: #000;
    padding: 10px;
    /* display: none; */
    border-radius: 3px;
}

.youraccount::before {
    content: '';
    position: absolute;
    top: -10px;
    left: 71%;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 15px solid white;
}

.youraccount h4 {
    margin: 5px 0;
}

#accountsandlists:hover .youraccount {
    display: block;
}

.youraccount ul {
    list-style-type: none;
}

#signout {
    transition: background-color .3s ease-in-out;
}

.header__option:hover {
    opacity: 1;
}

.header__option--cart {
    display: flex;
    align-items: flex-end;
}

.header_option--cart-text {
    margin-bottom: 4px;
}

.header--shopping-cart-items {
    padding: 5px;
    color: var(--dark-orange);
    font-size: 1.15rem;
}